.card {
    border-radius: 2px;
    height: 200px;
    margin: 1rem;
    position: relative;
    width: 200px;
    -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
            box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    margin: 8px 25px;
    border: 5px solid #f1f1f1;
  }

  .card:hover{
      -webkit-transform:scale(1.1);
          -ms-transform:scale(1.1);
              transform:scale(1.1); 
    }

  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
  }
  .card > .img-container > img {
    width: 100%;
    height: 100%;

  }

  



